import Vue from "vue";
import Router from "vue-router";
import Login from "../views/Login/Login.vue";

Vue.use(Router);

export default new Router({
  mode: "hash",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      redirect: "/login"
    },
    {
      path: "/login",
      name: "Login",
      component: Login
    },
    {
      path: "/menuList",
      name: "menuList",
      component: () => import("../views/Menu/MenuList.vue"),
      meta: {
        isLoginToken: true // 登录验证token
      },
      children: [
        {
          path: "/orderList",
          name: "orderList",
          meta: {
            isLoginToken: true
          },
          component: () =>
            import("../views/OrderManage/OrderList/OrderList.vue")
        },
        {
          path: "/about",
          name: "about",
          meta: {
            isLoginToken: true
          },
          component: () => import("../views/About.vue")
        },
        //管理员列表
        {
          path: "/admin",
          name: "ManageList",
          meta: {
            isLoginToken: true
          },
          component: () => import("../views/Manage/ManageList/ManageList.vue")
        },
        //角色管理
        {
          path: "/role",
          name: "RoleList",
          meta: {
            isLoginToken: true
          },
          component: () => import("../views/Manage/RoleList/RoleList.vue")
        },
        {
          path: "/assignRoleList",
          name: "assignRoleList",
          meta: {
            isLoginToken: true
          },
          component: () =>
            import("../views/Manage/AssignRoleList/AssignRoleList.vue")
        },
        //权限管理
        {
          path: "/permission",
          name: "accessConfig",
          meta: {
            isLoginToken: true
          },
          component: () =>
            import("../views/Manage/AccessConfig/AccessConfig.vue")
        },
        {
          path: "/form",
          name: "form",
          meta: {
            isLoginToken: true
          },
          component: () => import("../views/Form.vue")
        },

        {
          // app管理
          path: "/appManage",
          name: "appManage",
          meta: {
            isLoginToken: true
          },
          component: () =>
            import("../views/ChannelManage/AppManage/AppManage.vue")
        },
        {
          // 基本设置
          path: "/basic",
          name: "basicSetup",
          meta: {
            isLoginToken: true
          },
          component: () =>
            import("../views/SystemSettings/BasicSetup/BasicSetup.vue")
        },
        {
          // 邮件设置
          path: "/email",
          name: "emailSetting",
          meta: {
            isLoginToken: true
          },
          component: () =>
            import("../views/SystemSettings/EmailSetting/EmailSetting.vue")
        },
        {
          // 计划任务
          path: "/scheduleTask",
          name: "scheduleTask",
          meta: {
            isLoginToken: true
          },
          component: () =>
            import("../views/SystemSettings/ScheduleTask/ScheduleTask.vue")
        },
        {
          // 手机设置
          path: "/phone",
          name: "mobilePhoneSet",
          meta: {
            isLoginToken: true
          },
          component: () =>
            import("../views/SystemSettings/MobilePhoneSet/MobilePhoneSet.vue")
        },
        {
          // 修改密码
          path: "/admin/reset",
          name: "changePwd",
          meta: {
            isLoginToken: true
          },
          component: () =>
            import("../views/SystemSettings/ChangePwd/ChangePwd.vue")
        },
        {
          // 短信设置
          path: "/messageSet",
          name: "messageSet",
          meta: {
            isLoginToken: true
          },
          component: () =>
            import("../views/SystemSettings/MessageSet/MessageSet.vue")
        },
        {
          // 商户列表
          path: "/merchant",
          name: "accountManage",
          meta: {
            isLoginToken: true
          },
          component: () =>
            import("../views/BusinessManage/AccountManage/AccountManage.vue")
        },
      ]
    }
  ]
});
