import axios from "axios";
import { Promise } from "q";
import Vue from "vue";
import router from "../router/router";
import store from "../store/store.js";
// 解决post二次请求(options=>post)
import qs from "qs";

//设置默认请求头
axios.defaults.headers = {
  "X-Requested-With": "XMLHttpRequest"
};
/*第一层if判断生产环境和开发环境*/
if (process.env.NODE_ENV === "production") {
  /*第二层if，根据.env文件中的VUE_APP_FLAG判断是生产环境还是测试环境*/
  if (process.env.VUE_APP_FLAG === "pro") {
    //production 生产环境
    axios.defaults.baseURL = "http://api.xinggeyun.com";
  } else {
    //test 测试环境
    axios.defaults.baseURL = "https://api.emide.cn";
  }
} else {
  //dev 开发环境
  axios.defaults.baseURL = "http://192.168.2.200";
}

//拦截器
let cancel,
  promiseArr = {};
const CancelToken = axios.CancelToken;
//请求拦截器
axios.interceptors.request.use(
  config => {
    //发起请求时，取消掉当前正在进行的相同请求
    if (promiseArr[config.url]) {
      promiseArr[config.url]("操作取消");
      promiseArr[config.url] = cancel;
    } else {
      promiseArr[config.url] = cancel;
    }
    // 携带token
    // 登录请求不携带 /api/token/login
    if (config.url != "/api/token/login") {
      config.headers.Authorization = `Bearer ${window.localStorage.getItem(
        "token"
      )}`;
    }
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

// token 处理
let tokenHeader = options => {
  let { response, code } = options;
  let message = options.message ? options.message : response.message;
  if (response.code == code) {
    // 清除userInfo缓存 token
    window.localStorage.removeItem("userInfo");
    window.localStorage.removeItem("token");
    store.state.isLodingLogin = false;
    router.push("/login");
  }
};

//响应拦截器
axios.interceptors.response.use(
  response => {
    return response;
  },
  err => {
    // 异常处理
    if (err && err.response) {
      let response = err.response.data;
      console.log("统一错误处理: ", response);
      response.code == 402 ? tokenHeader({ response, code: "402" }) : "";
      new Vue().$message({
        showClose: true,
        duration: 1000,
        message: response.message,
        type: "warning"
      });
    }

    return Promise.reject(err);
  }
);

/**
 * 封装get方法
 * @param url
 * @param data
 * @returns {Promise}
 */
export function _get(url, params = {}) {
  return new Promise((resolve, reject) => {
    axios
      .get(url, {
        params
      })
      .then(res => {
        resolve(res);
      })
      .catch(err => {
        reject(err);
      });
  });
}

/**
 * 封装post请求
 * @param url
 * @param data
 * @returns {Promise}
 */
export function _post(url, data = {}) {
  return new Promise((resolve, reject) => {
    axios.post(url, data).then(
      response => {
        resolve(response.data);
      },
      err => {
        reject(err);
      }
    );
  });
}

/**
 * 封装patch请求
 * @param url
 * @param data
 * @returns {Promise}
 */
export function _patch(url, data = {}) {
  return new Promise((resolve, reject) => {
    axios.patch(url, data).then(
      response => {
        resolve(response.data);
      },
      err => {
        reject(err);
      }
    );
  });
}

/**
 * 封装put请求
 * @param url
 * @param data
 * @returns {Promise}
 */
export function _put(url, data = {}) {
  return new Promise((resolve, reject) => {
    axios.put(url, data).then(
      response => {
        resolve(response.data);
      },
      err => {
        reject(err);
      }
    );
  });
}

/**
 * 封装delete请求
 * @param url
 * @param data
 * @returns {Promise}
 */
export function _delete(url, params = {}) {
  return new Promise((resolve, reject) => {
    axios.delete(url, params).then(
      response => {
        resolve(response.data);
      },
      err => {
        reject(err);
      }
    );
  });
}

// 登录
export const userLogin = params => {
  return _post("/api/token/login", params);
};
// 获取用户信息
export const getUserMsg = () => {
  return _get("/api/token/me");
};
// 添加角色信息
export const addRoleMsg = params => {
  return axios.post("/api/role", params);
};
// 获取角色信息
export const getRoleMsg = () => {
  return axios.get("/api/role");
};
// 获取分页数据
export const getRolePageMsg = params => {
  return axios.get("/api/role", { params });
};
// 编辑角色信息
export const editRoleMsg = params => {
  return axios.put("/api/role/" + params.id, params);
};
//切换状态
export const switchRoleMsg = params => {
  return axios.get("api/role/select", params);
};


